<script>
import mensagem from '../../../components/forms/mensagem.vue';

import { NotaTransferenciaService } from '../../../services'

const notaTransferenciaService = new NotaTransferenciaService();

export default {
  components: { mensagem },

    data() {
        return {
            dados: [],
            filtro:{
                ordenar: 'id',
                ordem: 'desc',
                page: 1,
                per_page: 25,
                texto: '',
            },
            paginacao: {
                current_page: 0,
                total: 0,
                last_page: 0,
                from: 0,
                to: 0,
                per_page: 25
            },
            debounce: null,
            objetoModal: null,
            sucessoDeletar: null,
            erroDeletar: null
        };
    },
    mounted() {
            this.buscar();
    },
    methods: {
        debounceBusca: function(){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.navegar(1);
            }, 800);
        },
        
        buscar:function(){
            notaTransferenciaService.listar(this.filtro).then(
                response => {
                    this.$data.dados = response.data.data;
                    this.$data.paginacao = response.data;
                    this.$data.filtro.page = response.data.current_page;
                    this.$data.paginacao.per_page = response.data.per_page;
                }
            )
        },

        navegar: function(pagina){
            this.filtro.page = pagina;
            this.buscar();
        },
  
    }
};
</script>
